.profetional-main{
    width: 100%;
    text-transform: uppercase;
    background:'white'
}
.profetional-detail{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-transform: uppercase;
    background-color: white;
}
.multicolor-heading {
    font-size: 48px;
    font-weight: bold;
    text-align: center;
    -webkit-background-clip: text;
    color: black;
    position: relative;
    text-shadow: 4px 4px 4px rgba(255, 165, 0, 0.7), /* First shadow */
                  /* Second shadow */
}
.profetional-detail p{
    text-align: justify;
width: 90%;
font-display: swap;
}
.personal-profile{
    /* position: relative; */
    
}
.inputt-container{
    position: relative;
    width: 100%;
    height: 100px;
    /* border: 1px solid black; */
    border-radius: 50%;
}
.profile-inputt{
    height: 100px;
    width: 100px;
    border-radius: 50%;
   
    position: absolute;
    /* text-transform: uppercase; */
    left: 53%;
    top: 30px;
}
.profile-imgs{
    height: 100px;
    width: 100px;
    border-radius: 50%;
    position: absolute;
    border: 1px solid black;
    left: 50%;
    top: -20%;
    cursor: pointer;
}
.suggetion_to_object{
        max-width: 620px;
        padding: 0;
        margin: 5px 0;
        border: 1px solid transparent;
        border-color: rgb(164, 244, 250);
        border-radius: 4px;
        background-color: #ffffff; /* Add a background color */
        max-height: 100%;
        overflow-y: auto;
        z-index: 10; /* Ensure it stays on top */
}


.button-div{
    width: 100%;
}
.input-div{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* margin-left: 50px; */
    margin-top: 200px;
    /* margin-left: ; */
}
.button1 {
    position: relative;
    background: none;
    border: 2px solid black; /* Border clear dikhne lagega */
    border-radius: 8px;
    overflow: hidden;
    color: #fff;
    font-family: Phantomsans, sans-serif;
    font-size: 18px;
    padding: 10px 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s;
    padding: 1px;
  }
  
  
  

.button1:active,
.button1:hover {
outline: 0;
}

.button1 span {
background-color: rgb(5, 6, 45);
padding: 16px 24px;
border-radius: 6px;
width: 100%;
height: 100%;
transition: 300ms;
}

.button1:hover span {
background: none;
}

.button1:active {
transform: scale(0.9);
}

