.about-me-container {
    padding: 20px;
    max-width: 900px;
    margin: 0 auto;
    font-family: 'Arial', sans-serif;
    line-height: 1.6;
  }
  
  .about-me-container h1,.about-me-container h2 {
    background: linear-gradient(90deg, red, orange, yellow, green, blue, indigo, violet);
            -webkit-background-clip: text;
            color: transparent;
  }
 
  .img_div{
    display: flex;
    justify-content: space-between;
  }
  .img_div img{
    border-radius: 8px;
    box-shadow: 10px 0px 100px 10px pink;
    width: 45%;
  }
  .about-me-container p {
    margin-bottom: 16px;
    font-size: 1rem;
    color: #555;
  }
  
 .about-me-container h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .about-me-container h2 {
    font-size: 1.75rem;
    margin-top: 20px;
  }
  
  /* strong {
    color: #2c3e50;
  }
   */
  .about-me-call-to-action {
    background-color: #f5f5f5;
    padding: 20px;
    border-radius: 8px;
    text-align: center;
    margin-top: 30px;
  }
  
  .about-me-call-to-action p {
    font-size: 1.2rem;
    background-color: #fff;
    border: 2px solid whitesmoke;
    padding: 5px;
    border-radius: 8px;
    box-shadow: 10px 0px 100px 10px pink;
  }
  
  
  .about-me-belief{
    background-color: #fff;
    border: 2px solid whitesmoke;
    padding: 5px;
    border-radius: 8px;
    box-shadow: 10px 0px 100px 10px pink;
  }
  .mission_div{
    display: flex;
    justify-content: space-between;
    
  }
  .mission_div img{
    border-radius: 8px;
  }
  /* @media (min-width:769px){
img
  } */
  @media (max-width:768px) {
    .img_div{
      display: flex;
      flex-direction: column;
    }
    .img_div img{
      width: 100%;
      border-radius: 25px;
    }
  }